"use client";
// Import statements
import React, { useRef } from 'react';
import { useReactToPrint } from 'react-to-print';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import * as XLSX from 'xlsx';

const GercabGo = () => {
  const refs = {
    jejak: useRef(),
    laporan: useRef(),
    galeri: useRef(),
    agenda: useRef(),
  };

  const data = {
    jejak: [
      { id: 1, nama: 'Aktivitas 1', deskripsi: 'Deskripsi aktivitas hijau 1' },
      { id: 2, nama: 'Aktivitas 2', deskripsi: 'Deskripsi aktivitas hijau 2' },
    ],
    laporan: [
      { id: 1, kegiatan: 'Laporan Kegiatan 1', detail: 'Detail laporan 1' },
      { id: 2, kegiatan: 'Laporan Kegiatan 2', detail: 'Detail laporan 2' },
    ],
    galeri: [
      { id: 1, foto: 'Foto Galeri 1', deskripsi: 'Deskripsi galeri 1' },
      { id: 2, foto: 'Foto Galeri 2', deskripsi: 'Deskripsi galeri 2' },
    ],
    agenda: [
      { id: 1, kegiatan: 'Agenda 1', waktu: 'Tanggal dan waktu agenda 1' },
      { id: 2, kegiatan: 'Agenda 2', waktu: 'Tanggal dan waktu agenda 2' },
    ],
  };

  const exportToExcel = (rows, filename) => {
    const ws = XLSX.utils.json_to_sheet(rows);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, filename);
    XLSX.writeFile(wb, `${filename}.xlsx`);
  };

  const exportToPDF = async (ref, filename) => {
    const canvas = await html2canvas(ref.current);
    const imgData = canvas.toDataURL('image/png');
    const pdf = new jsPDF();
    const imgProps = pdf.getImageProperties(imgData);
    const pdfWidth = pdf.internal.pageSize.getWidth();
    const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
    pdf.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
    pdf.save(`${filename}.pdf`);
  };

  const handlePrint = (ref) => useReactToPrint({ content: () => ref.current })();

  const renderTable = (items, columns) => (
    <table className="min-w-full border text-sm">
      <thead className="bg-gray-100">
        <tr>
          {columns.map((col) => (
            <th key={col.key} className="border px-4 py-2 text-left font-medium">
              {col.header}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {items.map((item) => (
          <tr key={item.id} className="hover:bg-gray-50">
            {columns.map((col) => (
              <td key={col.key} className="border px-4 py-2">
                {item[col.key]}
              </td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );

  const sections = [
    {
      key: 'jejak',
      title: 'Jejak Hijau',
      columns: [
        { key: 'id', header: 'ID' },
        { key: 'nama', header: 'Nama Aktivitas' },
        { key: 'deskripsi', header: 'Deskripsi' },
      ],
    },
    {
      key: 'laporan',
      title: 'Lapor Lingkungan',
      columns: [
        { key: 'id', header: 'ID' },
        { key: 'kegiatan', header: 'Nama Kegiatan' },
        { key: 'detail', header: 'Detail' },
      ],
    },
    {
      key: 'galeri',
      title: 'Galeri Kearifan Lokal',
      columns: [
        { key: 'id', header: 'ID' },
        { key: 'foto', header: 'Foto' },
        { key: 'deskripsi', header: 'Deskripsi' },
      ],
    },
    {
      key: 'agenda',
      title: 'Agenda & Notifikasi Kegiatan',
      columns: [
        { key: 'id', header: 'ID' },
        { key: 'kegiatan', header: 'Kegiatan' },
        { key: 'waktu', header: 'Waktu' },
      ],
    },
  ];

  return (
    <div className="p-4 space-y-8">
      {sections.map(({ key, title, columns }) => (
        <div key={key} className="border p-4 rounded shadow bg-white">
          <div ref={refs[key]}>
            <h2 className="text-xl font-semibold mb-4">{title}</h2>
            {renderTable(data[key], columns)}
          </div>
          <div className="flex space-x-2 mt-4">
            <button
              onClick={() => exportToExcel(data[key], title.replace(/\s+/g, '_'))}
              className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
            >
              Export Excel
            </button>
            <button
              onClick={() => exportToPDF(refs[key], title.replace(/\s+/g, '_'))}
              className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
            >
              Export PDF
            </button>
            <button
              onClick={() => handlePrint(refs[key])}
              className="bg-gray-600 text-white px-4 py-2 rounded hover:bg-gray-700"
            >
              Cetak
            </button>
          </div>
        </div>
      ))}
    </div>
  );
};

export default GercabGo;
